import React, {useEffect, useState} from "react";
import './App.scss';
import QuestionsScreen from "../QuestionsScreen/QuestionScreen";
import {ChoseDifficulty} from "../ChoseDificulty/ChoseDifficulty";
import {ChoseQuestionsQuanity} from "../ChoseQuestionsQuantity/ChoseQuestionsQuanity";

const App = () => {
    const [categories, setCategories] = useState([])
    const [categoryChosen, setCategoryChosen] = useState(false)
    const [categoryID, setCategoryID] = useState(null)
    const [categoryDifficulty, setCategoryDifficulty] = useState(null);
    const [questionsQuantity, setQuestionQuantity] = useState(null)

    const resetGame = () => {
        setCategoryChosen(null);
        setCategoryID(null);
        setQuestionQuantity(null);
        setCategoryDifficulty(null);
    }

    useEffect(() => {

        fetch('https://opentdb.com/api_category.php')
            .then((result) => result.json())
            .then((data) => {
                setCategories(data.trivia_categories)
            })

    }, [])

    const handleClick = (id) => {
        setCategoryChosen(true);
        setCategoryID(id);

    }

    const handleDifficultyChose = (id) => {
        setCategoryDifficulty(id)
    }

    const handleQuestionQuantity = (number) => {
        setQuestionQuantity(number)
    }
    // return (

    // )

    // <QuestionsScreen categoryDifficulty={categoryDifficulty} id={categoryID}/>

    return categoryChosen !== true ? (
        <div className="container">
            <h1 className="header">Welcome to trivia api quiz!</h1>
            <h2 className='header'>Chose your category:</h2>
            <div className="category_box">
                {categories.map((trivia_categories, idx) => {

                        return (
                            <>
                                <button className="category_button" onClick={() => handleClick(trivia_categories.id)}
                                        key={idx} dangerouslySetInnerHTML={{__html: trivia_categories.name}}/>
                            </>
                        )
                    }
                )}
            </div>
        </div>
    ) : (categoryDifficulty === null ? (
            <>
                <ChoseDifficulty categoryDifficulty={categoryDifficulty} setCategoryDifficulty={setCategoryDifficulty}
                                 handleDificultyChose={handleDifficultyChose}/>
            </>
        ) : (questionsQuantity === null ? (
                <>
                    <ChoseQuestionsQuanity handleQuestionQuantity={handleQuestionQuantity}
                                           questionsQuantity={questionsQuantity}/>
                </>
            ) : (
                <>
                    <QuestionsScreen resetGame = {resetGame} categoryChosen={categoryChosen} setCategoryChosen={setCategoryChosen}
                                     categoryDifficulty={categoryDifficulty} id={categoryID}
                                     questionsQuantity={questionsQuantity}/>
                </>
            )
        )
    )


}

export default App;

